.navContainer {
    box-sizing: border-box;
    position: sticky;
    width: 75%;
    margin-left: auto;
    margin-right: auto;
    justify-content: space-between;
    display: inline-block;
    backdrop-filter: blur(5px);

    // background-color: blue;
    .navPages {
        display: grid;
        grid-auto-flow: column;
        column-gap: 12px;
    }


}